export const STAGE_WIDTH = 12;
export const STAGE_HEIGHT = 20;

export const createStage = () =>
  Array.from(Array(STAGE_HEIGHT), () =>
    new Array(STAGE_WIDTH).fill([0, "clear"])
  );

export const checkCollision = (player, stage, { x: moveX, y: moveY }) => {
  for (let y = 0; y < player.tetromino.length; y += 1) {
    for (let x = 0; x < player.tetromino.length; x += 1) {
      //check that we'er on a tetromino cell
      if (player.tetromino[y][x] !== 0) {
        if (
          //check that move is inside the game area y
          //don't go through bottom
          !stage[y + player.pos.y + moveY] ||
          // check our move is inside games area x
          !stage[y + player.pos.y + moveY][x + player.pos.x + moveX] ||
          // check that the destination isn't set to clear
          stage[y + player.pos.y + moveY][x + player.pos.x + moveX][1] !==
            "clear"
        ) {
          return true;
        }
      }
    }
  }
};
